<template>
    <div class='main'>
        <div class='form'>
            <el-form :inline="true" :model="form" class="demo-form-inline" >
                <el-form-item label="">
                    <el-date-picker  type="daterange" align="right" unlink-panels range-separator="-"
                                     start-placeholder="" end-placeholder="" @change="search()"
                                     style="width: 100%;" value-format="yyyy-MM-dd HH:mm:ss"  v-model='form.date' >
                    </el-date-picker>
                </el-form-item>
                <el-form-item class='search' label='' style='margin-left: 40px'>
                      <el-select v-model='form.select' @change='draw_List()'>
                          <el-option label=''></el-option>
                          <el-option value='0' label=''></el-option>
                          <el-option value='1' label=''></el-option>
                          <el-option value='2' label=''></el-option>
                      </el-select>
                </el-form-item>
            </el-form>
        </div>
        <div class='tableData'>
            <div class='table'>
                <el-table :data="data" style="width: 100%" :max-height="screenHeight">
                    <el-table-column prop="account" label=""  align="center"></el-table-column>
                    <el-table-column prop="money" label="()"  align="center"></el-table-column>
                    <el-table-column prop="create_time" label="" align="center"></el-table-column>
                    <el-table-column prop="audit_time" label=""   align="center"></el-table-column>
                    <el-table-column prop='audit_status'  label=""   align="center">
                        <template slot-scope="scope">
                            <span :class="[scope.row.audit_status==0 ? 'col-32373C' : scope.row.audit_status==1 ? 'col-00912D' : scope.row.audit_status==2 ? 'col-FF2828':'' ]">{{scope.row.audit_status==0 ? '' : scope.row.audit_status==1 ? '' : scope.row.audit_status==2 ? '':'' }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="address" label=""  align="center">
                        <template slot-scope="scope">
                            <a href="javascript:;" class='col-3498E9' v-if='scope.row.audit_status==2'  @click="See(scope.row.id)"></a>
                            <span v-else>--</span>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog title="" :visible.sync="Reject_ReasonState" width="30%" center>
            <div class='Reason'>
                {{Reason}}
            </div>
        </el-dialog>
        <component is='paging' :pageCount='pageCount' :currentChange='currentChange'></component>
    </div>
</template>
<script>
    import { drawList,checkReason } from '@/server/serverData'
    import paging from '@/views/common/paging'
    export default{
        name:'Main',
        data(){
          return{
              pageCount:1, //,
              currentPage:1,//
              form:{
                  date:'',
                  search:'',
                  select:''
              },
              data:[],
              screenHeight:window.innerHeight-300,
              Reject_ReasonState:false,
              Reason:'', //
          }
        },
        components:{
            paging
        },
        computed:{
            ApplyTime(){  /** ,,, **/
                return this.form.date instanceof Array ? {
                    startTime:this.form.date[0],
                    endTime: this.form.date[1]
                } :{
                    startTime:'',
                    endTime: ''
                }
            },
        },
        methods:{
            async draw_List(){
               let res = await drawList(
                 {
                     search:this.form.search,
                     start_time:this.ApplyTime.startTime,
                     end_time:this.ApplyTime.endTime,
                     page: this.currentPage,
                     status:this.form.select
                 }
               );
               if(res.data.code==1){
                   this.data=res.data.data.list;
                   this.pageCount=res.data.data.rows;
               }else{
                   this.data=[]
               }
            },
            /**  **/
            search(){
                /** 1, **/
                this.currentPage=1;
                this.pageCount=1;
                this.draw_List()
            },
           /**  **/
           currentChange(e){
             this.currentPage=e;
             this.draw_List()
           },
          async See(id){
               this.Reject_ReasonState=true
               let res = await checkReason({id:id})
               if(res.data.code==1){
                   this.Reason=res.data.data
               }else {
                   this.Reason=''
               }
           }
        },
       mounted(){
         this.draw_List()
        var that=this
        window.onresize = () => {
            return (() => {
                that.screenHeight = window.innerHeight - 370
        })()
        }
       }
    }
</script>
<style scoped lang='scss' rel="stylesheet/scss">
    .form{
        padding: 30px 0 0 30px;
        /deep/ .el-form-item__content{
          .el-input-group__append, .el-input-group__prepend{
            border: none;
            background-color: #3498E9;
            color: white;
            padding: 0 30px;
         }
       }
      .search{
          margin-right: 40px;
      }
    }
    .table{
        /deep/ .el-table th, .el-table tr{
            background-color: #D7E1E6;
        }
        /deep/ .el-table__row{
            td{
                .cell{
                    font-size: 12px;
                }
            }
        }
    }
    .paging{
        text-align: center;
        margin-top: 30px;
    }
    .Reason{
        min-height: 200px;
    }
</style>